import React, { useMemo } from 'react';
import { useSelector } from 'react-redux';

import Month from 'components/Deck/Month';
import CardsDeck from 'components/Deck/CardsDeck';
import { getYMDFromTimestamp } from 'utils/dates';

import C from 'constants/index';

import { MonthsBlock, Today } from 'styles/DateTable';

import { DateTypeWithWeek } from 'types';
import { State } from 'types/state';

const MonthsList = ({
  todayRef,
  months,
}: {
  todayRef: React.RefObject<HTMLElement>;
  months: DateTypeWithWeek[];
}): JSX.Element => {
  const todayTimestamp = useSelector((state: State) => state.ui.todayTimestamp);
  const today = useMemo(() => getYMDFromTimestamp(todayTimestamp), [todayTimestamp]);
  const cardsPosition = useSelector((state: State) => state.ui.cardsPosition);
  const [cardsYear, cardsMonth] = useMemo(() => cardsPosition.split('-').map((val) => Number(val)), [cardsPosition]);
  const todayStyle = useMemo(() => ({ left: `${today.day * C.DAY.WIDTH - C.DAY.WIDTH / 2}px` }), [today]);

  const MonthBlock = useMemo(
    () =>
      function MonthMemo(
        key: string,
        monthStyle: {
          width: string;
        },
        firstWeekDay: number,
        isToday: boolean,
        date: DateTypeWithWeek
      ): JSX.Element {
        return (
          <MonthsBlock key={key} style={monthStyle} firstDay={firstWeekDay}>
            {isToday && <Today ref={todayRef} style={todayStyle} />}
            <Month date={date} today={today} />
          </MonthsBlock>
        );
      },
    [todayRef, todayStyle, today]
  );

  return (
    <React.Fragment>
      {months.map((date: DateTypeWithWeek) => {
        const { year, month, day, firstWeekDay } = date;
        const key = `${year}-${month}`;
        const monthStyle = { width: `${day * C.DAY.WIDTH}px` };
        const isToday = today.year === year && today.month === month;
        if (cardsYear === year && cardsMonth === month) {
          return (
            <React.Fragment key={key}>
              <CardsDeck />
              {MonthBlock(key, monthStyle, firstWeekDay, isToday, date)}
            </React.Fragment>
          );
        }
        return MonthBlock(key, monthStyle, firstWeekDay, isToday, date);
      })}
    </React.Fragment>
  );
};

export default MonthsList;
