import styled from 'styled-components';

import C from 'constants/index';
import { getMonthBackgroud } from 'utils/ui';

export const DeckBlock = styled.section`
  font-size: 0;
  width: 100vw;
  height: 100%;
  overflow: scroll;
  white-space: nowrap;
  position: relative;
  user-select: none;
  -ms-overflow-style: none;
  &::-webkit-scrollbar {
    display: none;
  }
`;

export const DeckInner = styled.section`
  display: flex;
  min-height: calc(100vh - 45px);
`;

export const MonthsBlock = styled.div`
  display: inline-block;
  vertical-align: top;
  position: relative;
  box-shadow: inset 1px 0 0 rgba(0, 0, 0, 0.2);
  background-image: ${({ firstDay }: { firstDay: number }) => getMonthBackgroud(firstDay, C.DAY.WIDTH)};
  background-size: ${C.DAY.WIDTH}px 100%, ${C.DAY.WIDTH * 7}px 100%;
  .monthname {
    display: block;
    height: 54px;
    font-size: 12px;
    width: 100%;
    padding: 8px 12px 3px;
    position: sticky;
    top: 0;
    z-index: 8;
    cursor: default;
    background: #f7f8f9;
    box-sizing: border-box;
    color: #bbb;
    box-shadow: rgba(0, 0, 0, 0.2) 2px 2px 2px, rgba(0, 0, 0, 0.06) 1px 0 0 inset;
    pointer-events: all;
  }
  .days {
    top: 25px;
    position: sticky;
    margin-top: -100%;
    z-index: 9;
    cursor: default;
    user-select: none;
    span {
      width: 14px;
      display: inline-block;
      height: 14px;
      font-size: 12px;
      line-height: 14px;
      padding: 8px 7px 7px 9px;
      text-align: center;
      color: #999;
      position: relative;
      z-index: 1;
      &:before {
        position: absolute;
        width: 20px;
        height: 20px;
        left: 6px;
        top: 5px;
        border-radius: 50px;
        z-index: -1;
      }
      &.today {
        color: #fff;
        &:hover {
          color: #f00;
          &:after {
            background: #f00;
          }
        }
        &:before {
          content: '';
          background: #f00;
        }
      }
      &:hover {
        transform: translateY(3px);
        font-size: 10px;
        padding-bottom: 4px;
        &:before {
          content: '';
          background: transparent;
          border: 1px solid currentColor;
          left: 6px;
          top: 2px;
          border-radius: 3px;
          width: 18px;
          height: 18px;
        }
        &:after {
          content: attr(data-weekday);
          position: absolute;
          width: 20px;
          height: 8px;
          background: #999;
          font-size: 7px;
          color: #fff;
          line-height: 10px;
          top: 2px;
          border-radius: 3px 3px 0 0;
          left: 6px;
        }
      }
    }
  }
`;

export const Today = styled.mark`
  position: absolute;
  top: 0;
  width: 2px;
  height: 100%;
  background: linear-gradient(#f00, #8c0756);
  pointer-events: none;
`;

export const TodayScroll = styled.button`
  border: none;
  font-size: 12px;
  position: absolute;
  left: ${({ scrollPosition }: { scrollPosition: number }) => (scrollPosition < 0 ? '0' : '100%')};
  transform: ${({ scrollPosition }: { scrollPosition: number }) =>
    scrollPosition === 0
      ? 'translate(0)'
      : scrollPosition < 0
      ? 'translate(12px)'
      : 'translate(-100%) translate(-12px)'};
  background: #f7f8f9;
  box-shadow: 0 2px 6px -1px rgba(0, 0, 0, 0.2), inset 0 0 0 1px rgba(0, 0, 0, 0.1);
  border-radius: 4px;
  margin: 3px auto;
  line-height: 24px;
  padding: 0 7px;
  transition: box-shadow 0.1s, transform 0.1s, opacity 0.1s;
  cursor: pointer;
  z-index: 10;
  color: #555;
  user-select: none;
  opacity: ${({ scrollPosition }: { scrollPosition: number }) => (scrollPosition === 0 ? '0' : '1')};
  pointer-events: ${({ scrollPosition }: { scrollPosition: number }) => (scrollPosition === 0 ? 'none' : undefined)};
  &:hover {
    box-shadow: 0 3px 6px -1px rgba(0, 0, 0, 0.4), inset 0 0 0 1px rgba(0, 0, 0, 0.05);
  }
  &:before {
    content: ${({ scrollPosition }: { scrollPosition: number }) => scrollPosition < 0 && `"\\276E"`};
    color: #f00;
    margin: 0 7px 0 3px;
  }
  &:after {
    content: ${({ scrollPosition }: { scrollPosition: number }) => scrollPosition > 0 && `"\\276F"`};
    color: #f00;
    margin: 0 3px 0 7px;
  }
`;
