import C from 'constants/index';

import { placeholder } from './subscribtion';

export const putPlaceholder = (): void => {
  placeholder.update({
    started: true,
  });
};

export const movePlaceholder = (e: React.MouseEvent<HTMLElement, MouseEvent>): void => {
  const curTarget = e.currentTarget as HTMLUListElement;
  const target = e.target as HTMLLIElement;
  if (target.parentElement === curTarget) {
    const rect: ClientRect = curTarget.getBoundingClientRect();
    const childRect: ClientRect = target.getBoundingClientRect();
    const line = Math.floor((e.clientY - rect.top + curTarget.scrollTop - C.DAY.HEADER.HEIGHT) / C.CARD.VERTICAL_SPACE);
    if (line >= 0) {
      let month = -1;
      Array.from(curTarget.children).some((item) => {
        if (item instanceof HTMLElement && item.offsetWidth) {
          month = month + 1;
        }
        return item === target;
      });
      placeholder.update({
        x1: Math.trunc((target.offsetLeft + e.clientX - childRect.left) / C.DAY.WIDTH) * C.DAY.WIDTH,
        x2: e.clientX - rect.left + curTarget.scrollLeft,
        y1: C.DAY.HEADER.HEIGHT + line * C.CARD.VERTICAL_SPACE,
        date: `${month}-${Math.max(Math.floor((e.clientX - childRect.left) / C.DAY.WIDTH), 0) + 1}`,
        line,
      });
    }
  }
};

export const removePlaceholder = (e: React.MouseEvent<HTMLElement, MouseEvent>): void => {
  if (e.type === 'mouseup') {
    placeholder.update({
      started: false,
      done: true,
    });
  } else {
    const target = e.relatedTarget as HTMLElement;
    // TODO: check if we still need `data-form` attribute
    if (!target || !target.getAttribute || target.getAttribute('data-form') !== 'edit') {
      placeholder.update({
        started: false,
      });
    }
  }
};
