import { createStore, applyMiddleware, Store, CombinedState } from 'redux';
import createSagaMiddleware from 'redux-saga';
import { composeWithDevTools } from 'redux-devtools-extension';

import dataInit from 'data';
import { trackAuthChange } from 'data/auth';
import rootReducer from 'state/reducers';
import rootSaga from 'state/sagas';
import { authFetch, authOut, authSuccess } from 'state/actions/user';

import { Actions } from 'types/actions';
import { State } from 'types/state';
import { AuthSuccessProp } from 'types/actionsProps';

import { analytics } from 'utils/analytics';
// import { loadState, saveState } from './localStorage';

export default (): Store<CombinedState<State>, Actions> => {
  // Global initialization of the Firebase
  dataInit(analytics.saveAnalyticsState);

  // const persistedState = loadState();
  const sagaMiddleware = createSagaMiddleware();
  const middleware = applyMiddleware(sagaMiddleware);
  const composedMiddleware = composeWithDevTools({ trace: true, traceLimit: 25 })(middleware);

  const store = createStore(rootReducer, composedMiddleware);
  sagaMiddleware.run(rootSaga);

  // Starting authorization and provinging two callbacks for succesful & failed fetching auth data
  trackAuthChange({
    fetching: () => store.dispatch(authFetch()),
    success: (userData: AuthSuccessProp) => store.dispatch(authSuccess(userData)),
    out: () => store.dispatch(authOut()),
  });

  // const store = createStore(rootReducer, persistedState, composedMiddleware);
  // store.subscribe(() => {
  //   const { selection, ...stateRest } = store.getState();
  //   saveState(stateRest);
  // });

  return store;
};
