import React, { useState, useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux'; // TODO: shallowEqual

import { editCard } from 'state/actions/cards';
import { selectionCancel, selectionDelete } from 'state/actions/selection';
import { hasCardChanged, isCardValid } from 'utils/ui';

import { EditScreen as Screen, EditForm } from 'styles/EditScreen';

import EditFields from './EditFields';
import EditTitle from './EditTitle';

import { EditSelection, State } from 'types/state';
import { CardDispatch } from 'types/actions';

const DefaultState = {
  id: '',
  text: '',
  line: 0,
  from: '',
  until: '',
} as EditSelection;

const EditScreen = (): JSX.Element | null => {
  const dispatch = useDispatch();
  const selection = useSelector((state: State) => state.selection);
  const selectedElement = useSelector((state: State) => (selection.isNew ? undefined : state.cards[selection.id]));
  const [isExtraFieldsEnforced, enforceExtraFields] = useState(selectedElement !== undefined);
  const [newSelection, setNewSelection] = useState(DefaultState);
  const [isInvalid, setInvalid] = useState(false);
  useEffect(() => {
    if (isInvalid) {
      setTimeout(() => setInvalid(false), 500);
    }
  }, [isInvalid]);
  useEffect(() => {
    if (selection.id === '') {
      enforceExtraFields(selectedElement !== undefined);
    } else {
      setNewSelection({
        id: (selectedElement || selection).id,
        text: (selectedElement || selection).text,
        line: (selectedElement || selection).line,
        from: (selectedElement || selection).from,
        until: (selectedElement || selection).until,
        progress: (selectedElement || selection).progress,
      });
    }
    return () => {
      setNewSelection(DefaultState);
    };
  }, [selection, selectedElement]);

  if (selection.type === '' || newSelection.id === '') {
    return null;
  }

  const cardDispatch: CardDispatch = {
    save: () => {
      if (isCardValid(newSelection)) {
        return dispatch(hasCardChanged(newSelection, selectedElement) ? editCard(newSelection) : selectionCancel());
      } else {
        // TODO: Show user incorrect fields
        setInvalid(true);
        return;
      }
    },
    cancel: () => dispatch(selectionCancel()),
    delete: () => {
      dispatch(
        selectedElement
          ? selectionDelete({
              id: newSelection.id,
            })
          : selectionCancel()
      );
    },
  };

  const handleClick = (e: React.MouseEvent) => {
    if (e.target === e.currentTarget) {
      cardDispatch.cancel();
    }
  };

  const hasExtraFields = isExtraFieldsEnforced || Boolean(selectedElement);
  return (
    <Screen onClick={handleClick} data-form="edit">
      <EditForm className={isInvalid ? 'invalid' : undefined}>
        <EditTitle
          selection={newSelection}
          setSelection={setNewSelection}
          cardDispatch={cardDispatch}
          enforceExtraFields={hasExtraFields ? undefined : enforceExtraFields}
        />
        {hasExtraFields ? (
          <EditFields
            selection={newSelection}
            setSelection={setNewSelection}
            cardDispatch={cardDispatch}
            setFocus={isExtraFieldsEnforced}
          />
        ) : null}
      </EditForm>
    </Screen>
  );
};

export default React.memo(EditScreen);
