import React, { useMemo } from 'react';
import { useDispatch, useSelector } from 'react-redux';

import { getProgressBackgroud, getCardStatus } from 'utils/ui';
import { selectCard, selectionDelete } from 'state/actions/selection';
import { removePlaceholder } from 'utils/placeholderActions';
import CardStatus from 'assets/CardStatus';
import { passID } from 'utils/subscribtion';
import { getDataString } from 'utils/dates';

import CLR from 'constants/colors';

import { CardBlock, CardBlockProgress, CardBlockText } from 'styles/CardBlock';

import { Card, State } from 'types/state';

type Props = {
  card: Card;
  style: {
    width: string;
    top: string;
  };
};

const RoadmapCard = ({ card, style }: Props): JSX.Element => {
  const dispatch = useDispatch();
  const todayTimestamp = useSelector((state: State) => state.ui.todayTimestamp);
  const todayString = useMemo(() => getDataString(todayTimestamp), [todayTimestamp]);
  const typeS = useMemo(() => getCardStatus(card.from, card.until, todayString, card.progress), [card, todayString]);
  const progS = useMemo(() => getProgressBackgroud(card.progress, typeS ? CLR.statusColors[typeS] : ''), [card, typeS]);

  const clickCard = (e: React.MouseEvent<HTMLButtonElement, MouseEvent>): void => {
    if (
      (e.target as HTMLButtonElement)?.parentElement?.parentElement?.classList.contains('drag') ||
      e.altKey ||
      e.ctrlKey ||
      e.metaKey ||
      e.shiftKey
    ) {
      return;
    }
    dispatch(
      selectCard({
        id: card.id,
      })
    );
  };

  const handleDoubleClick = (e: React.MouseEvent<HTMLButtonElement, MouseEvent>) => {
    if (e.altKey) {
      dispatch(
        selectionDelete({
          id: card.id,
        })
      );
    }
  };

  const handleMouseDown = () => {
    passID.set(card.id);
  };

  // TODO: Optimize rendering reducing new objects
  return (
    <CardBlock
      style={style}
      onClick={clickCard}
      onDoubleClick={handleDoubleClick}
      onMouseEnter={removePlaceholder}
      onTouchStart={handleMouseDown}
      onMouseDown={handleMouseDown}
      title={card.text}
    >
      <CardStatus type={typeS} />
      <CardBlockProgress style={progS} />
      <CardBlockText>{card.text}</CardBlockText>
    </CardBlock>
  );
};

export default React.memo(RoadmapCard);
