import React from 'react';

const Day = ({
  day,
  weekDay,
  tip,
  isToday,
}: {
  day: number;
  weekDay: string;
  tip: string;
  isToday: boolean;
}): JSX.Element => {
  const className = isToday ? 'today' : undefined;
  return (
    <span className={className} data-weekday={weekDay} title={tip}>
      {day}
    </span>
  );
};

export default Day;
