import firebase from 'firebase';
import { put, fork, take, call, select, TakeEffect, ForkEffect } from 'redux-saga/effects';

import { fetchAccountDetails, fetchRoadmapMeta } from 'data/data';
import { createAccount } from 'data/newUser';
import { setAccountRoadmaps } from 'state/actions/user';

import { AuthSuccess } from 'types/actions';
import { Meta, State } from 'types/state';

function* fetchUserRoadmapsData(userID: string) {
  const snapshot = (yield call(fetchAccountDetails, userID)) as firebase.database.DataSnapshot;
  const value = (yield snapshot.val()) as {
    roadmaps?: State['user']['roadmaps'];
  } | null;
  if (!value) {
    const user = (yield select((state: State) => state.user)) as State['user'];
    yield call(createAccount, user);
  }
  if (value?.roadmaps) {
    const roadmapIDs = Object.keys(value.roadmaps);
    if (roadmapIDs) {
      const metas = {} as State['metas'];
      for (const roadmapID of roadmapIDs) {
        const snapshot = (yield call(fetchRoadmapMeta, roadmapID)) as firebase.database.DataSnapshot;
        metas[roadmapID] = snapshot.val() as Meta;
      }
      yield put(setAccountRoadmaps(value.roadmaps, metas));
    } else {
      yield put(setAccountRoadmaps({}, {}));
    }
  } else {
    yield put(setAccountRoadmaps({}, {}));
  }
}

export function* watchAuth(): Generator<TakeEffect | ForkEffect<void>, void, AuthSuccess> {
  while (true) {
    const action = yield take('AUTH_SUCCESS');
    if (typeof action.payload?.id === 'string') {
      yield fork(fetchUserRoadmapsData, action.payload.id);
    }
  }
}
