import firebase from 'firebase/app';
import { eventChannel, EventChannel } from 'redux-saga';

export const getDataConnectionStatus = (callback: (status: boolean) => void): EventChannel<boolean> =>
  eventChannel(() => {
    const listener = firebase.database().ref('.info/connected');
    listener.on('value', (snapshot) => {
      callback(snapshot.val());
    });
    return () => {
      listener.off();
    };
  });
