export type Card = {
  id: string;
  line: number;
  text: string;
  from: string;
  until: string;
  progress?: number;
};

export type MileColors =
  | 'purple'
  | 'violet'
  | 'indigo'
  | 'blue'
  | 'azur'
  | 'green'
  | 'yellow'
  | 'orange'
  | 'fire'
  | 'pink';

export type Milestone = {
  id: string;
  text: string;
  date: string;
  from?: never;
  until?: never;
  progress?: never;
  color: MileColors;
};

export type Meta = {
  createdAt: number;
  from: string;
  name: string;
  until: string;
};

export enum ElementType {
  card = 'card',
  milestone = 'milestone',
  none = '',
}

export type List<Type> = {
  [key: string]: Type;
};

export interface PersistentState {
  cards: List<Card>;
  milestones: List<Milestone>;
}

export type Cards = PersistentState['cards'];
export type Milestones = PersistentState['milestones'];

export type SelectedElement = Card | Milestone | undefined;

export type EditSelection = Card;

export interface Selection extends EditSelection {
  isNew: boolean;
  type: ElementType;
}

export interface State extends PersistentState {
  selection: Selection;
  user: {
    id: string;
    name: string;
    email: string;
    photoURL: string;
    fetchig: boolean;
    roadmaps: {
      [key: string]: boolean;
    };
    roadmapsSet: boolean;
  };
  metas: List<Meta>;
  ui: {
    currentRoadmapID: string;
    todayTimestamp: number;
    cardsPosition: string;
    fromOffset: string;
    untilOffset: string;
  };
}
