import produce from 'immer';

import initialState from 'state/initialState';
import { getDataString, getFromUntil, getUIOffset } from 'utils/dates';

import { Actions } from 'types/actions';
import { State } from 'types/state';

type StatePart = State['ui'];

export default (state: StatePart = initialState.ui, action: Actions): StatePart => {
  switch (action.type) {
    case 'SET_CURRENT_ROADMAP':
      return produce(state, (nextState: StatePart) => {
        return {
          ...nextState,
          currentRoadmapID: action.payload.currentRoadmapID,
        };
      });
    case 'REMOVE_CURRENT_ROADMAP':
      return produce(state, (nextState: StatePart) => {
        return {
          ...nextState,
          currentRoadmapID: '',
          cardsPosition: '',
          fromOffset: '',
          untilOffset: '',
        };
      });
    case 'AUTH_OUT':
      return initialState.ui;
    case 'ROADMAP_DATA_FETCH':
      // TODO: Ignore if the data fetch is actually data submit
      if (state.cardsPosition !== '' && state.fromOffset !== '' && state.untilOffset !== '') {
        return state;
      }
      return produce(state, (nextState: StatePart) => {
        // TODO: store these values in DB and use them also in UI as default offset
        let { from, until } = getFromUntil(action.payload.cards);
        const todayString = getDataString(nextState.todayTimestamp);
        const { fromOffset, untilOffset } = getUIOffset(from, until, todayString);
        if (!from || !until) {
          from = from || todayString;
          until = until || todayString;
        }
        return {
          ...nextState,
          cardsPosition: `${from.slice(0, 7)}-01`,
          fromOffset: fromOffset,
          untilOffset: untilOffset,
        };
      });
    case 'SET_UI_OFFSET':
      return state.fromOffset === action.payload.fromOffset && state.untilOffset === action.payload.untilOffset
        ? state
        : produce(state, (nextState: StatePart) => {
            return {
              ...nextState,
              ...action.payload,
            };
          });
    case 'SET_TODAY_TIMESTAMP':
      return produce(state, (nextState: StatePart) => {
        return {
          ...nextState,
          todayTimestamp: action.payload.timestamp,
        };
      });
    default:
      return state;
  }
};
