import C from 'constants/index';

import { DateType, DateTypeWithWeek, FullDateType } from 'types';
import { MoveCard } from 'types/actions';
import { Cards } from 'types/state';

import { getExtraMonths } from 'utils/ui';

export const getTwoDigitDate = (number: number): string => `0${number}`.slice(-2);
// export const getDataString = (date: DateType): string => `${date.year}-${date.month}-${date.day}`;

export const getYMDFromTimestamp = (timestamp: number | Date): DateType => {
  const date = timestamp instanceof Date ? timestamp : new Date(timestamp);
  return {
    year: date.getUTCFullYear(),
    month: date.getUTCMonth() + 1,
    day: date.getUTCDate(),
  };
};

export const getYMDWFromTimestamp = (timestamp: number | Date): DateTypeWithWeek => {
  const date = getYMDFromTimestamp(timestamp);
  const firstDay = new Date(timestamp);
  firstDay.setUTCHours(0);
  firstDay.setUTCDate(1);
  return {
    ...date,
    firstWeekDay: firstDay.getUTCDay(),
  };
};

export const getWeekNumber = (timestamp: number): number => {
  const date = new Date(timestamp);
  date.setUTCDate(date.getUTCDate() + 4 - (date.getUTCDay() || 7));
  const yearStart = new Date(Date.UTC(date.getUTCFullYear(), 0, 1));
  const dateDiff = date.getTime() - yearStart.getTime();
  return Math.ceil((dateDiff / C.TIME.MILLISECONDS_IN_DAY + 1) / 7);
};
export const getWeekdayFromTimestamp = (timestamp: number, long: boolean = true): string => {
  return new Date(timestamp).toLocaleDateString(C.LOCALE, { weekday: long ? 'long' : 'narrow' });
};

export const getMonthNameFromTimestamp = (timestamp: number): string => {
  return new Date(timestamp).toLocaleDateString(C.LOCALE, { month: 'long' });
};

export const getMonthName = (date: DateType): string => {
  return new Date(date.year, date.month - 1, date.day).toLocaleDateString(C.LOCALE, { month: 'long' });
};

export const getDateForTooltip = (date: DateType): [string, string] => {
  const dateObject = new Date(date.year, date.month - 1, date.day);
  return [
    dateObject.toLocaleDateString(C.LOCALE, { weekday: 'short' }),
    dateObject.toLocaleDateString(C.LOCALE, { day: 'numeric', month: 'long', year: 'numeric' }),
  ];
};

// For now we assume weekends are only Saturdays & Sundays
export const isWeekend = (timestamp: number): boolean => new Date(timestamp).getDay() % 6 === 0;

export const getTodayTimestamp = (): number => {
  const today = new Date();
  return Date.UTC(today.getFullYear(), today.getMonth(), today.getDate());
};

export const getDataString = (timestamp?: number | Date): string => {
  if (timestamp instanceof Date) {
    return timestamp.toISOString().slice(0, 10);
  }
  return new Date(timestamp || getTodayTimestamp()).toISOString().slice(0, 10);
};

export const getLocalString = (timestamp: number = getTodayTimestamp()): string => new Date(timestamp).toLocaleString();

export const getFullDateFromTimestamp = (timestamp: number, isToday: boolean = false): FullDateType => ({
  date: getYMDFromTimestamp(timestamp),
  dateString: getDataString(timestamp),
  monthName: getMonthNameFromTimestamp(timestamp),
  weekDayName: getWeekdayFromTimestamp(timestamp),
  weekDayShort: getWeekdayFromTimestamp(timestamp, false),
  weekNumber: getWeekNumber(timestamp),
  isWeekend: isWeekend(timestamp),
  isToday,
});

export const getDatesArray = (columnSize: number, todayTimestamp: number): FullDateType[] =>
  Array(columnSize)
    .fill(todayTimestamp)
    .map((dateString: number, index: number) => {
      const timestamp = dateString + (index - columnSize / 2) * C.TIME.MILLISECONDS_IN_DAY;
      return getFullDateFromTimestamp(timestamp, todayTimestamp === timestamp);
    });

export const extendDatesArray = (datesArray: FullDateType[], extraDays: number, backward?: boolean): FullDateType[] => {
  const lastDay = datesArray[backward ? 0 : datesArray.length - 1];
  const lastTimestamp = Date.UTC(lastDay.date.year, lastDay.date.month - 1, lastDay.date.day);
  const extraArray = Array(extraDays)
    .fill(lastTimestamp)
    .map((dateString: number, index: number) => {
      const timestamp = dateString + (backward ? index - extraDays : index + 1) * C.TIME.MILLISECONDS_IN_DAY;
      return getFullDateFromTimestamp(timestamp, false);
    });
  return backward ? [...extraArray, ...datesArray] : [...datesArray, ...extraArray];
};

export const getDaysDiff = (date1: string | Date, date2: string | Date): number => {
  if (date1 instanceof Date && date2 instanceof Date) {
    return (date2.getTime() - date1.getTime()) / C.TIME.MILLISECONDS_IN_DAY;
  }
  return (new Date(date2).getTime() - new Date(date1).getTime()) / C.TIME.MILLISECONDS_IN_DAY;
};

export const getDaysInterval = (date1: string, date2: string): number => getDaysDiff(date1, date2) + 1;

export const getDateFromDiff = (date: string, diff: number): string =>
  getDataString(new Date(date).getTime() + diff * C.TIME.MILLISECONDS_IN_DAY);

export const getDaysInMonth = (year: number, month: number): number => new Date(year, month, 0).getDate();

export const getDaysDiffBetweenMonths = (date1: string, date2: string): number => {
  const d1 = new Date(date1);
  const d2 = new Date(date2);
  d1.setDate(1);
  d2.setDate(1);
  return getDaysDiff(d1, d2);
};

export const getExpandedDate = (year: number, month: number, offsetMonths: number): string => {
  const isFuture = offsetMonths > 0;
  const date = new Date(Date.UTC(year, month - (isFuture ? 0 : 1) + offsetMonths, isFuture ? 0 : 1));
  return getDataString(date);
};

export const getMilisecondsUntilTomorrow = (days: number = 1): number => {
  const today = new Date();
  const otherDay = new Date(today.getFullYear(), today.getMonth(), today.getDate() + days);
  return otherDay.getTime() - today.getTime();
};

export const getNewDates = ({ direction, dateDiff }: MoveCard['payload'], dates: string[]): string[] => {
  switch (direction) {
    case -1:
      return [getDateFromDiff(dates[0], dateDiff), dates[1]];
    case 1:
      return [dates[0], getDateFromDiff(dates[1], dateDiff)];
    default:
      return dates.map((date) => getDateFromDiff(date, dateDiff));
  }
};

export const getMonthList = (from: string, until: string, date?: DateTypeWithWeek, swap = 1): DateTypeWithWeek[] => {
  const [fromYear, fromMonth] = from.split('-');
  const [untilYear, untilMonth] = until.split('-');
  const { year, month } = until === '' && date ? date : { year: untilYear, month: untilMonth };
  const yearMonth1 = [Number(fromYear), Number(fromMonth)];
  const yearMonth2 = [Number(year), Number(month)];
  const min = swap > 0 ? yearMonth1 : yearMonth2;
  const max = swap < 0 ? yearMonth1 : yearMonth2;
  const extraMonth = until === '' ? 0 : 1; // We need this extra month to add equal number of months before and after
  const skipOneMonthForFuture = swap === -1 ? 1 : 0;

  const yearsDiff = max[0] - min[0];
  const monthsDiff = max[1] - min[1] + extraMonth + yearsDiff * 12;

  // TODO: Ensure there is sufficient months offset around initially rendered months to avoid addition at scroll
  return Array(monthsDiff)
    .fill(0)
    .map((_, index) => getYMDWFromTimestamp(new Date(min[0], min[1] + index + skipOneMonthForFuture)));
};

export const getExtendedMonthList = (months: DateTypeWithWeek[], i: number, offset: string): DateTypeWithWeek[] => {
  const swap = i === 0 ? 1 : -1;
  const newMonths = getMonthList(offset, '', months[i], swap);
  Array.prototype[i ? 'push' : 'unshift'].apply(months, newMonths);
  return months;
};

export const getFromUntil = (cards: Cards): { from: string; until: string } =>
  Object.values(cards).reduce(
    ({ from, until }, card) => {
      if (from === '') {
        return { from: card.from, until: card.until };
      }
      return {
        from: from.localeCompare(card.from) < 0 ? from : card.from,
        until: until.localeCompare(card.until) > 0 ? until : card.until,
      };
    },
    { from: '', until: '' }
  );

export const getUIOffset = (
  from: string,
  until: string,
  todayString: string
): { fromOffset: string; untilOffset: string } => {
  const min = from ? (from.localeCompare(todayString) <= 0 ? from : todayString) : todayString;
  const max = until ? (todayString.localeCompare(until) < 0 ? until : todayString) : todayString;

  const extraMonths = getExtraMonths();
  return {
    fromOffset: getExpandedDate(Number(min.slice(0, 4)), Number(min.slice(5, 7)), -extraMonths),
    untilOffset: getExpandedDate(Number(max.slice(0, 4)), Number(max.slice(5, 7)), extraMonths),
  };
};
