import { getTwoDigitDate } from './dates';

import { DateTypeWithWeek } from 'types';

export type SubscribtionStatus = {
  started?: boolean;
  x1?: number;
  x2?: number;
  y1?: number;
  line?: number;
  date?: string;
  done?: boolean;
};
export type Callback<T> = (status: T) => void;
export type SetNotification<T> = (fn: Callback<T>) => void;
export type Subscribtion<T> = (status: T) => {
  notify: SetNotification<T>;
  update: Callback<T>;
  setMonths: Callback<DateTypeWithWeek[]>;
};

export const subscribtion: Subscribtion<SubscribtionStatus> = (init) => {
  let status = init;
  let months: DateTypeWithWeek[] = [];
  let callItBack: Callback<SubscribtionStatus> | undefined;
  return {
    notify: (fn) => {
      callItBack = fn;
    },
    update: (update) => {
      if (update.date) {
        const [m, d] = update.date.split('-').map((x) => parseInt(x, 10));
        update.date = m ? `${months[m].year}-${getTwoDigitDate(months[m].month)}-${getTwoDigitDate(d)}` : status.date;
      } else {
        update.date = status.date;
      }
      status = update;
      callItBack && callItBack(status);
    },
    setMonths: (newMonths: DateTypeWithWeek[]) => {
      months = newMonths;
    },
  };
};

export const placeholderInitState = {
  started: false,
  x1: 0,
  x2: 0,
  y1: 0,
  line: 0,
  date: '',
  done: false,
};
export const placeholder = subscribtion(placeholderInitState);

export const passID = (() => {
  let id = '';
  return {
    set: (newID: string) => (id = newID),
    get: () => id,
  };
})();
