import firebase from 'firebase/app';

import { AuthFetch, AuthSuccess, AuthOut } from 'types/actions';
import { AuthSuccessProp } from 'types/actionsProps';
import { writeUserLastActive } from 'data/user';

export const handleAuth = (): void => {
  if (typeof firebase.auth === 'function') {
    const provider = new firebase.auth.GoogleAuthProvider();
    firebase.auth().useDeviceLanguage();
    void firebase.auth().signInWithRedirect(provider);
  }
};

export const handleAuthOut = (userID: string): void => {
  writeUserLastActive(userID);
  if (typeof firebase.auth === 'function') {
    void firebase.auth().signOut();
  }
};

export const trackAuthChange = ({
  fetching,
  success,
  out,
}: {
  fetching: () => AuthFetch;
  success: (userData: AuthSuccessProp) => AuthSuccess;
  out: () => AuthOut;
}): void => {
  fetching();
  firebase.auth().onAuthStateChanged((user) => {
    if (user && user.email) {
      success({
        id: user.uid,
        name: user.displayName || '',
        email: user.email,
        photoURL: user.photoURL || '',
      });
    } else {
      out();
    }
  });
};
