import styled, { css } from 'styled-components';

import color from 'constants/colors';
import { rotate } from 'styles/Keyframes';

export const SpinnerMixin = css`
  width: ${({ size = 50 }: { size?: number }) => `${size}px`};
  height: ${({ size = 50 }: { size?: number }) => (size ? `${size}px` : '50px')};
  margin: auto;
  border: ${({ size = 50 }: { size?: number }) => `${Math.ceil(size / 10)}px solid transparent`};
  border-top-color: ${({ size }: { size?: number }) => (size ? color.header.acid : '#ccc')};
  border-radius: 50%;
  animation: ${rotate} 1s linear infinite;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
`;

export const Spinner = styled.div`
  ${SpinnerMixin}
`;
