import { SetTodayTimestamp, SetUIOffset } from 'types/actions';

export const setUIOffset = (offset: SetUIOffset['payload']): SetUIOffset => ({
  type: 'SET_UI_OFFSET',
  payload: {
    ...offset,
  },
});

export const setTodayTimestamp = (timestamp: number): SetTodayTimestamp => ({
  type: 'SET_TODAY_TIMESTAMP',
  payload: {
    timestamp,
  },
});
