import { EditCard, MoveCard } from 'types/actions';
import { MoveCardProp } from 'types/actionsProps';
import { EditSelection } from 'types/state';

export const editCard = (props: EditSelection): EditCard => ({
  type: 'EDIT_CARD',
  payload: {
    ...props,
  },
});

export const moveCard = (props: MoveCardProp): MoveCard => ({
  type: 'MOVE_CARD',
  payload: {
    ...props,
  },
});
