import produce from 'immer';

import initialState from '../initialState';

import { Actions } from 'types/actions';
import { State } from 'types/state';

type StatePart = State['user'];

export default (state: StatePart = initialState.user, action: Actions): StatePart => {
  switch (action.type) {
    case 'AUTH_FETCH':
      return produce(state, (nextState: StatePart) => {
        return {
          ...nextState,
          fetchig: true,
        };
      });
    case 'AUTH_SUCCESS':
      return produce(state, (nextState: StatePart) => {
        return {
          ...nextState,
          ...action.payload,
          fetchig: false,
        };
      });
    case 'AUTH_OUT':
      return initialState.user;
    case 'SET_ACCOUNT_ROADMAPS':
      return produce(state, (nextState: StatePart) => {
        return {
          ...nextState,
          roadmaps: {
            ...nextState.roadmaps,
            ...action.payload.roadmaps,
          },
          roadmapsSet: true,
        };
      });
    case 'REMOVE_ACCOUNT_ROADMAP':
      return produce(state, (nextState: StatePart) => {
        delete nextState.roadmaps[action.payload.roadmapID];
        return nextState;
      });
    default:
      return state;
  }
};
