import produce from 'immer';

import { getNewDates } from 'utils/dates';

import initialState from '../initialState';

import { Actions } from 'types/actions';
import { State } from 'types/state';

type StatePart = State['cards'];

export default (state = initialState.cards, action: Actions): StatePart => {
  switch (action.type) {
    case 'EDIT_CARD':
      return produce(state, (nextState: StatePart) => {
        if (!Number.isInteger(action.payload.progress)) {
          delete action.payload.progress;
        }
        nextState[action.payload.id] = { ...action.payload };
        return nextState;
      });
    case 'MOVE_CARD':
      return produce(state, (nextState: StatePart) => {
        const card = nextState[action.payload.id];
        const [from, until] = getNewDates(action.payload, [card.from, card.until]);
        const line = Math.max(card.line + action.payload.lineDiff, 0);
        nextState[action.payload.id] = {
          ...nextState[action.payload.id],
          line,
          from,
          until,
        };
        return nextState;
      });
    case 'SELECTION_DELETE':
      if (state[action.payload.id] !== undefined) {
        return produce(state, (nextState: StatePart) => {
          delete nextState[action.payload.id];
          return nextState;
        });
      }
      return state;
    case 'ROADMAP_DATA_FETCH':
      return {
        ...action.payload.cards,
      };
    case 'REMOVE_CURRENT_ROADMAP':
    case 'AUTH_OUT':
      return initialState.cards;
    default:
      return state;
  }
};
