import produce from 'immer';

import initialState from '../initialState';

import { Actions } from 'types/actions';
import { State } from 'types/state';

type StatePart = State['metas'];

export default (state: StatePart = initialState.metas, action: Actions): StatePart => {
  switch (action.type) {
    case 'SET_ACCOUNT_ROADMAPS':
      return produce(state, (nextState: StatePart) => {
        return {
          ...nextState,
          ...action.payload.meta,
        };
      });
    case 'AUTH_OUT':
      return initialState.metas;
    default:
      return state;
  }
};
