import React from 'react';
import { useSelector } from 'react-redux';

import { handleAuthOut } from 'data/auth';

import { DropdownGroup } from 'styles/Common/Dropdown';

import { State } from 'types/state';

const Dropdown = (): JSX.Element => {
  const user = useSelector((state: State) => state.user);
  const authOut = () => handleAuthOut(user.id);
  const noProfileEditYet = () => alert('Profile editing will be available in the near future');

  return (
    <DropdownGroup>
      <li onClick={noProfileEditYet}>{user.name}</li>
      <li onClick={authOut}>Log out</li>
    </DropdownGroup>
  );
};

export default React.memo(Dropdown);
