import React, { useState, useEffect } from 'react';
import { useSelector } from 'react-redux';
import Check from '@material-ui/icons/CheckCircle';
import Cancel from '@material-ui/icons/Cancel';

import { State } from 'types/state';
import { getDataConnectionStatus } from 'data/connection';

const ConnectionStatus = (): JSX.Element | null => {
  const user = useSelector((state: State) => state.user);
  const [isConnected, setConnected] = useState(false);
  useEffect(() => {
    const notify = (status: boolean) => setConnected(status);
    const connectionChannel = getDataConnectionStatus(notify);
    return () => {
      connectionChannel.close();
    };
  }, []);

  return user.id ? isConnected ? <Check className="online" /> : <Cancel className="offline" /> : null;
};

export default React.memo(ConnectionStatus);
