import React, { useMemo, useEffect } from 'react';
import { useSelector } from 'react-redux';

import { RoadmapTitle } from 'styles/Header';

import { State } from 'types/state';

const RoadmapList = ({ title }: { title: string }): JSX.Element | null => {
  const currentRoadmapID = useSelector((state: State) => state.ui.currentRoadmapID);
  const metas = useSelector((state: State) => state.metas);
  const roadmapName = useMemo(() => (metas[currentRoadmapID] || { name: title }).name, [metas, currentRoadmapID]);
  useEffect(() => {
    if (document.title !== roadmapName) {
      document.title = roadmapName === title ? title : `${roadmapName}  ${title}`;
    }
  }, [roadmapName]);

  return <RoadmapTitle>{roadmapName}</RoadmapTitle>;
};

export default React.memo(RoadmapList);
