import React, { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';

import { selectionCancel } from 'state/actions/selection';
import { placeholder } from 'utils/subscribtion';
import Deck from 'components/Deck/Deck';

import { State } from 'types/state';

const Roadmap = (): JSX.Element | null => {
  const currentRoadmapID = useSelector((state: State) => state.ui.currentRoadmapID);
  const dispatch = useDispatch();
  useEffect(() => {
    const cancelAction = (e: KeyboardEvent): void => {
      if (e.key.toLocaleLowerCase() === 'escape') {
        e.preventDefault();
        dispatch(selectionCancel());
        placeholder.update({
          started: false,
        });
      }
    };
    document.addEventListener('keydown', cancelAction);
    return () => {
      document.removeEventListener('keydown', cancelAction);
    };
  }, [dispatch]);

  return currentRoadmapID ? <Deck /> : null;
};

export default React.memo(Roadmap);
