import React, { useState, useEffect } from 'react';
import { useDispatch } from 'react-redux';

import Overview from 'components/Overview/Overview';
import SingleLineInput from 'components/Overview/SingleLineInput';
import { addAccountRoadmap } from 'state/actions/user';

const OverviewScreen = (): JSX.Element => {
  const [isShowingInput, showInput] = useState(false);
  const [roadmapName, setRoadmapName] = useState('');
  const dispatch = useDispatch();
  useEffect(() => {
    if (roadmapName) {
      dispatch(addAccountRoadmap(roadmapName));
    }
  }, [dispatch, roadmapName]);
  return (
    <React.Fragment>
      <Overview showInput={showInput} />
      <SingleLineInput
        isShowingInput={isShowingInput}
        showInput={showInput}
        roadmapName={roadmapName}
        setRoadmapName={setRoadmapName}
      />
    </React.Fragment>
  );
};

export default React.memo(OverviewScreen);
