import React, { useMemo } from 'react';
import { useSelector } from 'react-redux';
import Save from '@material-ui/icons/Save';
import Revert from '@material-ui/icons/NotInterested';
import Delete from '@material-ui/icons/DeleteForever';

import CardStatus from 'assets/CardStatus';
import { getCardStatus } from 'utils/ui';
import { getDataString } from 'utils/dates';

import { Button } from 'styles/Form/Button';
import { EditExtra, EditExtraActions, EditExtraFields, Fieldset } from 'styles/EditScreen';

import Range from './Range';
import Switch from './Switch';
import DateInput from './DateInput';

import { EditSelection, State } from 'types/state';
import { CardDispatch } from 'types/actions';

type EditFieldsProps = {
  selection: EditSelection;
  setSelection: React.Dispatch<React.SetStateAction<EditSelection>>;
  cardDispatch: CardDispatch;
  setFocus: boolean;
};

const EditFields = ({ selection, setSelection, cardDispatch, setFocus }: EditFieldsProps): JSX.Element | null => {
  const todayTimestamp = useSelector((state: State) => state.ui.todayTimestamp);
  const todayString = useMemo(() => getDataString(todayTimestamp), [todayTimestamp]);

  const progress = selection.progress || 0;
  const isProgressOn = Number.isFinite(selection.progress);
  const setProgress = (value: number) =>
    setSelection({
      ...selection,
      progress: value,
    });
  const setProgressOn = (value: boolean) => {
    if (value) {
      selection.progress = progress;
    } else {
      delete selection.progress;
    }
    setSelection({ ...selection });
  };
  const setDate = (type: 'from' | 'until') => (e: React.ChangeEvent<HTMLInputElement>) =>
    setSelection({
      ...selection,
      [type]: e.target.value,
    });

  const setFrom = setDate('from');
  const setUntil = setDate('until');

  const { from, until } = selection;

  return (
    <EditExtra>
      <EditExtraFields>
        <DateInput date={from} setDate={setFrom} save={cardDispatch.save} title="From" setFocus={setFocus} />
        <DateInput date={until} setDate={setUntil} save={cardDispatch.save} title="Until" />

        <Fieldset>
          <Switch id="showProgress" value={isProgressOn} toggle={setProgressOn} title="Completion" />
          <Range
            id="progress"
            min={0}
            max={100}
            disabled={!isProgressOn}
            value={progress}
            change={setProgress}
            save={cardDispatch.save}
            join={true}
          />
          {from && until && typeof progress === 'number' && (
            <CardStatus
              type={getCardStatus(from, until, todayString, progress)}
              className={isProgressOn ? 'active' : undefined}
            />
          )}
        </Fieldset>
      </EditExtraFields>
      <EditExtraActions>
        <Button onClick={cardDispatch.save} title="Save changes" action={'primary'}>
          <Save />
          <span>Save</span>
        </Button>
        <Button onClick={cardDispatch.cancel} title="Revert changes">
          <Revert />
          <span>Revert</span>
        </Button>
        <Button onClick={cardDispatch.delete} title="Delete card" action={'danger'}>
          <Delete />
          <span>Delete</span>
        </Button>
      </EditExtraActions>
    </EditExtra>
  );
};

export default React.memo(EditFields);
