import React from 'react';

import { getDateForTooltip, getMonthName } from 'utils/dates';
import Day from 'components/Deck/Day';

import { DateType } from 'types';

const Month = ({ date, today }: { date: DateType; today: DateType }): JSX.Element => {
  const monthName = `${getMonthName(date)} ${date.year}`;
  const days = Array(date.day)
    .fill(date)
    .map(({ year, month }: { year: number; month: number }, i: number) => {
      const day = i + 1;
      const isToday = today.year === year && today.month === month && today.day === day;
      const [weekDay, tip] = getDateForTooltip({ year, month, day });
      return <Day key={day} day={day} weekDay={weekDay} tip={tip} isToday={isToday} />;
    });

  return (
    <React.Fragment>
      <div className="monthname">{monthName}</div>
      <div className="days">{days}</div>
    </React.Fragment>
  );
};

export default React.memo(Month);
