export const setRecurrentEvent = (fn: () => void, timeout: number): (() => void) => {
  let eventActive = true;
  const recurrentEvent = (func: () => void, time: number): void => {
    setTimeout(() => {
      if (eventActive) {
        func();
        recurrentEvent(func, time);
      }
    }, time);
  };
  return (() => {
    fn();
    recurrentEvent(fn, timeout);
    return () => {
      eventActive = false;
    };
  })();
};
