import {
  AuthFetch,
  AuthSuccess,
  AuthOut,
  SetAccountRoadmaps,
  AddAccountRoadmap,
  RemoveAccountRoadmap,
  SetCurrentRoadmap,
  RemoveCurrentRoadmap,
  RoadmapDataFetch,
} from 'types/actions';
import { AuthSuccessProp } from 'types/actionsProps';
import { PersistentState, State } from 'types/state';

export const authFetch = (): AuthFetch => ({
  type: 'AUTH_FETCH',
});

export const authSuccess = (userData: AuthSuccessProp): AuthSuccess => ({
  type: 'AUTH_SUCCESS',
  payload: {
    ...userData,
  },
});

export const authOut = (): AuthOut => ({
  type: 'AUTH_OUT',
});

export const setAccountRoadmaps = (roadmaps: State['user']['roadmaps'], meta: State['metas']): SetAccountRoadmaps => ({
  type: 'SET_ACCOUNT_ROADMAPS',
  payload: {
    roadmaps,
    meta,
  },
});

export const addAccountRoadmap = (roadmapName: string): AddAccountRoadmap => ({
  type: 'ADD_ACCOUNT_ROADMAP',
  payload: {
    roadmapName,
  },
});

export const removeAccountRoadmap = (roadmapID: string): RemoveAccountRoadmap => ({
  type: 'REMOVE_ACCOUNT_ROADMAP',
  payload: {
    roadmapID,
  },
});

export const setCurrentRoadmap = (currentRoadmapID: string): SetCurrentRoadmap => ({
  type: 'SET_CURRENT_ROADMAP',
  payload: {
    currentRoadmapID,
  },
});

export const removeCurrentRoadmap = (): RemoveCurrentRoadmap => ({
  type: 'REMOVE_CURRENT_ROADMAP',
});

export const roadmapDataFetch = (roadmapData: PersistentState): RoadmapDataFetch => ({
  type: 'ROADMAP_DATA_FETCH',
  payload: {
    ...roadmapData,
  },
});
