import React, { useRef, useLayoutEffect } from 'react';

import { Fieldset, FormLabel, FormInput } from 'styles/EditScreen';

type DateInputProps = {
  date?: string;
  setDate: (e: React.ChangeEvent<HTMLInputElement>) => void;
  save: () => void;
  title: string;
  setFocus?: boolean;
};

const DateInput = ({ date, setDate, save, title, setFocus }: DateInputProps): JSX.Element | null => {
  const inputRef = useRef<HTMLInputElement>(null);
  useLayoutEffect(() => {
    if (inputRef !== null && inputRef.current instanceof HTMLInputElement) {
      inputRef.current.focus();
      inputRef.current.select();
    }
  }, []);

  const name = title.toLowerCase();
  const handleKeyDown = (e: React.KeyboardEvent<HTMLInputElement>) => {
    if (e.key.toLocaleLowerCase() === 'enter') {
      e.preventDefault();
      save();
    }
  };

  return date === undefined ? null : (
    <Fieldset>
      <FormLabel htmlFor={name}>{title}</FormLabel>
      <FormInput
        id={name}
        ref={setFocus ? inputRef : undefined}
        value={date}
        onChange={setDate}
        onKeyDown={handleKeyDown}
        required
        pattern="([12]\d{3}-(0[1-9]|1[0-2])-(0[1-9]|[12]\d|3[01]))"
      />
    </Fieldset>
  );
};

export default React.memo(DateInput);
