import React, { useLayoutEffect, useRef } from 'react';

import { autoscaleTextarea } from 'utils/ui';

import { EditScreen as Screen, EditForm, EditEntry } from 'styles/EditScreen';

const handleChange = (e: React.ChangeEvent<HTMLTextAreaElement>) => {
  autoscaleTextarea(e.target as HTMLTextAreaElement);
};

type Props = {
  isShowingInput: boolean;
  showInput: (value: boolean) => void;
  roadmapName: string;
  setRoadmapName: (value: string) => void;
};

const SingleLineInput = ({ isShowingInput, showInput, roadmapName, setRoadmapName }: Props): JSX.Element | null => {
  const inputRef = useRef<HTMLTextAreaElement>(null);
  useLayoutEffect(() => {
    if (inputRef !== null && inputRef.current !== null) {
      autoscaleTextarea(inputRef.current, true);
    }
  }, []);

  if (!isShowingInput) {
    return null;
  }

  const handleClick = (e: React.MouseEvent) => {
    if (e.target === e.currentTarget) {
      showInput(false);
    }
  };

  const handleKeyDown = (e: React.KeyboardEvent<HTMLTextAreaElement>) => {
    if (e.key.toLocaleLowerCase() === 'enter') {
      e.preventDefault();
      const { value } = e.currentTarget;
      if (value !== '' && roadmapName !== value) {
        setRoadmapName(value);
      }
      showInput(false);
    } else if (e.key.toLocaleLowerCase() === 'escape') {
      showInput(false);
    }
  };

  return (
    <Screen onClick={handleClick}>
      <EditForm>
        <EditEntry
          ref={inputRef}
          autoFocus={true}
          onChange={handleChange}
          onKeyDown={handleKeyDown}
          placeholder="Roadmap name"
        />
      </EditForm>
    </Screen>
  );
};

export default React.memo(SingleLineInput);
