import C from 'constants/index';

import { putPlaceholder, movePlaceholder, removePlaceholder } from './placeholderActions';
import { passID } from './subscribtion';

import { MoveCardProp } from 'types/actionsProps';

type EventHandler = (e: React.MouseEvent<HTMLElement, MouseEvent>) => void;

type CardActions = {
  pressInZone: EventHandler;
  moveInZone: EventHandler;
  releaseInZone: EventHandler;
  enterZone: EventHandler;
  leaveZone: EventHandler;
};

const [mXFix, mYFix] = C.CARD.CARDDECK_MOUSE_POSITION_FIX;

const getMousePositionFromCardDeck = (button: HTMLButtonElement, target: HTMLElement): number[] => {
  if (button === target) {
    return [button.offsetLeft, button.offsetTop];
  }
  if (target instanceof HTMLButtonElement) {
    return [target.offsetLeft, target.offsetTop];
  }
  const cardDeck = button.parentElement as HTMLLIElement;
  return [target.offsetLeft - cardDeck.offsetLeft - mXFix, target.offsetTop - cardDeck.offsetTop - mYFix];
};

const getCoords = (
  e: React.MouseEvent<HTMLElement, MouseEvent>,
  coord: number[],
  el: HTMLButtonElement
): [number, number] => {
  const target = e.target as HTMLElement;
  const nativeEvent = e.nativeEvent;
  const [mX, mY] = getMousePositionFromCardDeck(el, target);
  const x = mX + nativeEvent.offsetX - coord[0];
  const y = mY + nativeEvent.offsetY - coord[1];
  return [x, y];
};

export default (moveCard: (moveCard: MoveCardProp) => void): CardActions => {
  const card = {
    coord: [0, 0],
    width: 0,
    el: null as HTMLButtonElement | null,
  };
  const resize = {
    limit: 0,
    pressX: 0,
    direction: 0,
    extraSize: 0,
  };
  let dateTable: HTMLUListElement | null = null;
  let setAim = false;

  const removeDateTable = () => {
    if (dateTable) {
      dateTable.classList.remove('drag');
      dateTable = null;
    }
  };

  const clearDesk = (immidiate: boolean, wasResized: boolean = false): void => {
    if (!card.el || !dateTable) {
      return;
    }
    if (wasResized) {
      resize.limit = 0;
      resize.pressX = 0;
      resize.direction = 0;
      resize.extraSize = 0;
      dateTable.style.cursor = '';
    }
    card.el.style.pointerEvents = '';
    card.el.style.transform = '';
    card.el.style.opacity = '';
    card.el.style.zIndex = '';
    card.el = null;
    if (immidiate) {
      removeDateTable();
    } else {
      setTimeout(removeDateTable, 10);
    }
  };

  const pressInZone = (e: React.MouseEvent<HTMLElement, MouseEvent>): void => {
    if (card.el) {
      return;
    }
    if (e.target instanceof HTMLButtonElement) {
      dateTable = e.currentTarget as HTMLUListElement;
      dateTable.classList.add('drag');
      card.el = e.target;
      const { clientWidth, offsetLeft, offsetTop } = card.el;
      const { offsetX, offsetY, clientX } = e.nativeEvent;
      if (offsetX < C.CARD.RESIZE_TOLERANCE || clientWidth - offsetX < C.CARD.RESIZE_TOLERANCE) {
        dateTable.style.cursor = 'ew-resize';
        resize.pressX = clientX;
        resize.direction = offsetX < C.CARD.RESIZE_TOLERANCE ? -1 : 1;
        card.width = parseInt(card.el.style.width, 10);
        resize.limit = (card.width - C.CARD.HORIZONTAL_SPACE) / -C.DAY.WIDTH;
        return;
      }
      card.coord = [offsetLeft + offsetX, offsetTop + offsetY];
    } else if (e.target instanceof HTMLDivElement) {
      setAim = true;
      putPlaceholder();
    }
  };
  const moveInZone = (e: React.MouseEvent<HTMLElement, MouseEvent>): void => {
    if (!card.el) {
      if (e.target instanceof HTMLDivElement) {
        movePlaceholder(e);
      }
      return;
    }
    if (!card.el.style.transform) {
      card.el.style.pointerEvents = 'none';
      card.el.style.zIndex = '5';
    }
    if (resize.direction === 0) {
      const [x, y] = getCoords(e, card.coord, card.el);
      if (e.clientY < C.DAY.HEADER.MILE_HEIGHT) {
        card.el.style.opacity = '0';
      } else {
        card.el.style.transform = `translate(${x}px, ${y}px)`;
        if (card.el.style.opacity !== '') {
          card.el.style.opacity = '';
        }
      }
    } else {
      const { clientX } = e.nativeEvent;
      const diffX = clientX - resize.pressX;
      const extra =
        resize.direction < 0
          ? Math.round((C.CARD.RESIZE_TOLERANCE - diffX) / C.DAY.WIDTH)
          : Math.ceil((diffX - C.CARD.RESIZE_TOLERANCE) / C.DAY.WIDTH);
      resize.extraSize = Math.max(extra, resize.limit);
      const extraX = resize.extraSize * C.DAY.WIDTH;
      card.el.style.width = `${card.width + extraX}px`;
      if (resize.direction < 0) {
        card.el.style.transform = `translateX(${-extraX}px)`;
      }
    }
  };
  const releaseInZone = (e: React.MouseEvent<HTMLElement, MouseEvent>): void => {
    const id = passID.get();
    if (resize.direction !== 0) {
      if (resize.extraSize) {
        if (id) {
          moveCard({
            id,
            direction: resize.direction,
            lineDiff: 0,
            dateDiff: resize.extraSize * resize.direction,
          });
        } else {
          throw Error('Card ID is missing, card will not be resized.');
        }
      }
      clearDesk(card.el === e.target, true);
      return;
    }
    if (card.el) {
      if (card.el.style.opacity === '0') {
        return;
      }
      if (card.el !== e.target) {
        const [x, y] = getCoords(e, card.coord, card.el);
        if (id) {
          const lineDiff = Math.round(y / C.CARD.VERTICAL_SPACE);
          const dateDiff = Math.round(x / C.DAY.WIDTH);
          if (lineDiff || dateDiff) {
            moveCard({
              id,
              direction: 0,
              lineDiff,
              dateDiff,
            });
          }
        } else {
          throw Error('Card ID is missing, card will not be moved.');
        }
      }
      clearDesk(card.el === e.target);
    } else if (setAim) {
      setAim = false;
      removePlaceholder(e);
    }
  };
  const leaveZone = (e: React.MouseEvent<HTMLElement, MouseEvent>): void => {
    if (e.type === 'mouseout') {
      if (
        e.relatedTarget instanceof HTMLButtonElement ||
        e.relatedTarget instanceof HTMLHtmlElement ||
        e.relatedTarget instanceof HTMLSpanElement ||
        e.relatedTarget === null
      ) {
        removePlaceholder(e);
        setAim = false;
      }
      return;
    }
    if (card.el && !(e.target instanceof HTMLButtonElement) && resize.direction === 0) {
      card.el.style.opacity = '0';
    }
  };
  const enterZone = (): void => {
    if (setAim) {
      setAim = false;
    }
    if (card.el && card.el.style.opacity !== '') {
      card.el.style.opacity = '';
    }
  };

  return {
    pressInZone,
    moveInZone,
    releaseInZone,
    enterZone,
    leaveZone,
  };
};
