import React, { useState, ChangeEvent } from 'react';

import { LabelWarp, RangeInput, FormInput } from 'styles/EditScreen';

type Props = {
  id: string;
  min: number;
  max: number;
  value: number;
  disabled: boolean;
  change?: (value: number) => void;
  save: () => void;
  title?: string;
  join?: boolean;
};

const Range = ({ id, min, max, value, disabled, change, save, title, join }: Props): JSX.Element => {
  const [val, setVal] = useState(value);
  const handleChange = (e: ChangeEvent<HTMLInputElement>): void => {
    const newVal = Math.min(Math.max(parseInt(e.target.value, 10), min), max);
    if (!Number.isNaN(newVal)) {
      setVal(newVal);
      if (change) {
        change(newVal);
      }
    }
  };
  const handleKeyDown = (e: React.KeyboardEvent<HTMLInputElement>) => {
    if (e.key.toLocaleLowerCase() === 'enter') {
      e.preventDefault();
      save();
    }
  };
  return (
    <React.Fragment>
      {typeof title === 'string' ? <label htmlFor={id}>{title}</label> : null}
      <LabelWarp join={join}>
        <RangeInput id={id} min={min} max={max} disabled={disabled} value={val} onChange={handleChange} />
      </LabelWarp>
      <FormInput value={val} onChange={handleChange} onKeyDown={handleKeyDown} disabled={disabled} />
    </React.Fragment>
  );
};

export default React.memo(Range);
