import { fork, take, select, TakeEffect, SelectEffect, ForkEffect } from 'redux-saga/effects';

import { updateRemoteCardData } from 'data/data';

import { CardEditActions } from 'types/actions';
import { Card, State } from 'types/state';

type Actions = CardEditActions | Card | State['user']['roadmaps'] | undefined;

export function* watchCardChange(): Generator<TakeEffect | SelectEffect | ForkEffect<unknown>, void, Actions | string> {
  while (true) {
    const action = (yield take(['EDIT_CARD', 'MOVE_CARD', 'SELECTION_DELETE'])) as CardEditActions;
    const card = (yield select((state: State) => state.cards[action.payload.id])) as Card | undefined;
    const currentRoadmapID = (yield select((state: State) => state.ui.currentRoadmapID)) as string;
    if (currentRoadmapID) {
      yield fork(updateRemoteCardData, currentRoadmapID, card || { id: action.payload.id });
    }
  }
}
