import produce from 'immer';

import initialState from '../initialState';

import { Actions } from 'types/actions';
import { State, ElementType } from 'types/state';

type StatePart = State['selection'];

export default (state = initialState.selection, action: Actions): StatePart => {
  switch (action.type) {
    case 'NEW_CARD':
    case 'SELECT_CARD':
      return produce(state, (nextState: StatePart) => ({
        ...nextState,
        ...action.payload,
        type: ElementType.card,
      }));
    case 'SELECTION_CANCEL':
    case 'SELECTION_DELETE':
    case 'EDIT_CARD':
      return produce(state, () => initialState.selection);
    case 'AUTH_OUT':
      return initialState.selection;
    default:
      return state;
  }
};
