import produce from 'immer';

import initialState from '../initialState';

import { Actions } from 'types/actions';
import { State } from 'types/state';

type StatePart = State['milestones'];

export default (state = initialState.milestones, action: Actions): StatePart => {
  switch (action.type) {
    case 'ADD_MILESTONE':
      return produce(state, (nextState: StatePart) => {
        nextState[action.payload.id] = { ...action.payload };
        return nextState;
      });
    case 'DELETE_MILESTONE':
      return produce(state, (nextState: StatePart) => {
        Object.values(nextState).forEach((milestone) => {
          if (milestone.date === action.payload.date) {
            delete nextState[milestone.id];
          }
        });
        return nextState;
      });
    case 'ROADMAP_DATA_FETCH':
      return {
        ...action.payload.milestones,
      };
    case 'REMOVE_CURRENT_ROADMAP':
    case 'AUTH_OUT':
      return initialState.milestones;
    default:
      return state;
  }
};
