import React, { useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { useHistory } from 'react-router-dom';

import Header from 'components/Header/Header';
import Routes from 'components/Core/Routes';
import { getMilisecondsUntilTomorrow, getTodayTimestamp } from 'utils/dates';
import { setTodayTimestamp } from 'state/actions/ui';

import { State } from 'types/state';
import { MainBlock } from 'styles/Main';

let timeout: number;

const Core = (): JSX.Element => {
  const todayTimestamp = useSelector((state: State) => state.ui.todayTimestamp);
  const dispatch = useDispatch();
  useEffect(() => {
    const manageTimer = () => {
      if (document.hidden) {
        window.clearTimeout(timeout);
      } else {
        const newTimestamp = getTodayTimestamp();
        if (todayTimestamp !== newTimestamp) {
          dispatch(setTodayTimestamp(newTimestamp));
        } else {
          timeout = window.setTimeout(() => {
            dispatch(setTodayTimestamp(getTodayTimestamp()));
          }, getMilisecondsUntilTomorrow());
        }
      }
    };
    manageTimer();
    document.addEventListener('visibilitychange', manageTimer);
    return () => {
      document.removeEventListener('visibilitychange', manageTimer);
    };
  }, [dispatch, todayTimestamp]);

  const fetchig = useSelector((state: State) => state.user.fetchig);
  const userID = useSelector((state: State) => state.user.id);
  const history = useHistory();
  useEffect(() => {
    if (!fetchig && userID === '') {
      history.push('/'); // User isn't defined (logged out), but fetching has stopped
    }
  }, [history, fetchig, userID]);

  return (
    <React.Fragment>
      <Header />
      <MainBlock>{fetchig ? null : <Routes />}</MainBlock>
    </React.Fragment>
  );
};

export default React.memo(Core);
