import React, { useRef, useState, useLayoutEffect, useMemo } from 'react';
import { useDispatch, useSelector } from 'react-redux';

import { getDaysDiffBetweenMonths, getExpandedDate, getMonthList, getExtendedMonthList } from 'utils/dates';
import { getExtraMonths, scrollToToday } from 'utils/ui';
import cardActions from 'utils/cardActions';
import { placeholder } from 'utils/subscribtion';
import { moveCard } from 'state/actions/cards';
import Placeholder from 'components/Placeholder/Placeholder';
import MonthsList from 'components/Deck/MonthsList';
import { setUIOffset } from 'state/actions/ui';

import C from 'constants/index';

import { DeckBlock, DeckInner, TodayScroll } from 'styles/DateTable';

import { MoveCardProp } from 'types/actionsProps';
import { State } from 'types/state';
import { DateTypeWithWeek } from 'types/index';

const Deck = (): JSX.Element | null => {
  const dispatch = useDispatch();
  const listRef = useRef<HTMLElement>(null);
  const todayRef = useRef<HTMLElement>(null);
  const scroll = useRef({
    days: 0,
    left: 0,
    fire: false,
    ready: true,
    position: 0,
  });
  const [scrollPosition, setScrollPosition] = useState(0);
  const [months, setMonths] = useState([] as DateTypeWithWeek[]);
  const hasMonths = useMemo(() => months.length > 0, [months.length]);
  const fromOffset = useSelector((state: State) => state.ui.fromOffset);
  const untilOffset = useSelector((state: State) => state.ui.untilOffset);
  const lastOffset = useRef({ fromOffset, untilOffset });
  const updateMonths = (newMonths: DateTypeWithWeek[]): void => {
    setMonths(newMonths);
    placeholder.setMonths(newMonths);
  };
  useLayoutEffect(() => {
    // This effect makes the initial scroll to `today`
    if (scroll.current.ready && listRef.current && todayRef.current) {
      scrollToToday(listRef.current, todayRef.current);
      scroll.current.position = listRef.current.scrollLeft;
      scroll.current.ready = false;
    }
  }, [hasMonths]);
  useLayoutEffect(() => {
    // This effect moves deck's scroll while user scrolls and when new months are added in front of existing dates
    if (scroll.current.fire && listRef.current) {
      listRef.current.scrollLeft = scroll.current.left;
      scroll.current.fire = false;
    }
  }, [fromOffset, untilOffset]);
  useMemo(() => {
    // This function tracks offsets updates and adds new months before or after existing set
    if (fromOffset && untilOffset) {
      if (months.length === 0) {
        // First time
        updateMonths(getMonthList(fromOffset, untilOffset));
      } else {
        if (lastOffset.current.fromOffset !== fromOffset) {
          updateMonths(getExtendedMonthList(months, 0, fromOffset));
        } else if (lastOffset.current.untilOffset !== untilOffset) {
          updateMonths(getExtendedMonthList(months, months.length - 1, untilOffset));
        }
      }
    }
    lastOffset.current = {
      fromOffset,
      untilOffset,
    };
  }, [months, fromOffset, untilOffset]);

  const { pressInZone, moveInZone, releaseInZone, enterZone, leaveZone } = useMemo(() => {
    const remoteDispatch = (cardDetails: MoveCardProp) => dispatch(moveCard(cardDetails));
    return cardActions(remoteDispatch);
  }, [dispatch]);

  const showScrollButton = () => {
    const list = listRef.current;
    const scrollCurrent = scroll.current;
    if (list && scrollCurrent) {
      const todayElement = todayRef.current;
      if (todayElement) {
        const todayLeft = todayElement.getBoundingClientRect().left;
        if (todayLeft < 0) {
          setScrollPosition(-1);
        } else if (todayLeft > list.offsetWidth) {
          setScrollPosition(1);
        } else {
          setScrollPosition(0);
        }
      }
      scrollCurrent.position = list.scrollLeft;
    }
  };

  const renderPastOrFuture = (direction: number) => {
    if (direction < 0 && listRef.current) {
      const firstMonth = months[0];
      const newDate = getExpandedDate(firstMonth.year, firstMonth.month, -getExtraMonths());
      dispatch(
        setUIOffset({
          fromOffset: newDate,
          untilOffset: untilOffset,
        })
      );
      const extraLeftOffset = getDaysDiffBetweenMonths(newDate, fromOffset) * C.DAY.WIDTH;
      scroll.current.left = listRef.current.scrollLeft + extraLeftOffset;
      scroll.current.fire = true;
    } else {
      const lastMonth = months[months.length - 1];
      const newDate = getExpandedDate(lastMonth.year, lastMonth.month, getExtraMonths());
      dispatch(
        setUIOffset({
          fromOffset: fromOffset,
          untilOffset: newDate,
        })
      );
    }
  };

  const handleClick = (e: React.MouseEvent<HTMLButtonElement>): void => {
    if (listRef.current && todayRef.current) {
      scrollToToday(listRef.current, todayRef.current, true);
      e.currentTarget.blur();
    }
  };

  const scollDeck = (e: React.UIEvent<HTMLElement>) => {
    const deck = e.currentTarget as HTMLElement;
    const days = Math.floor(deck.scrollLeft / C.DAY.WIDTH);
    if (scroll.current.days !== days) {
      scroll.current.days = days;
      showScrollButton();
      // TODO: If window width stays the same daysVisible & daysTotal can be cached
      const daysVisible = Math.ceil(deck.offsetWidth / C.DAY.WIDTH);
      const daysTotal = Math.ceil(deck.scrollWidth / C.DAY.WIDTH);
      if (days <= daysVisible) {
        renderPastOrFuture(-1);
      } else if (days >= daysTotal - daysVisible * 2) {
        renderPastOrFuture(1);
      }
    }
  };

  // eslint-disable-next-line react-hooks/exhaustive-deps
  const Months = useMemo(() => <MonthsList todayRef={todayRef} months={months} />, [months, months.length, todayRef]);

  if (!months.length) {
    return null;
  }

  return (
    <React.Fragment>
      <TodayScroll scrollPosition={scrollPosition} tabIndex={scrollPosition ? undefined : -1} onClick={handleClick}>
        Today
      </TodayScroll>
      <DeckBlock
        ref={listRef}
        onScroll={scollDeck}
        onMouseEnter={enterZone}
        onMouseOut={leaveZone}
        onMouseLeave={leaveZone}
      >
        <DeckInner onMouseDown={pressInZone} onMouseMove={moveInZone} onMouseUp={releaseInZone}>
          {Months}
          <Placeholder />
        </DeckInner>
      </DeckBlock>
    </React.Fragment>
  );
};

export default React.memo(Deck);
