import React, { useState, useMemo, useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';

import { newCard } from 'state/actions/selection';
import { placeholder, placeholderInitState } from 'utils/subscribtion';
import { isAimOnCard, getPlaceholderData, getPlaceholderWidth } from 'utils/ui';
import { getDateFromDiff } from 'utils/dates';

import { PlaceholderBlock, PlaceholderContent } from 'styles/Placeholder';

import { State, ElementType } from 'types/state';

const Placeholder = (): JSX.Element => {
  const [aim, setAim] = useState(placeholderInitState);
  const cards = useSelector((state: State) => state.cards);
  const memoizedCards = useMemo(() => Object.values(cards), [cards]);
  const isAimHoversCard = useMemo(() => isAimOnCard(aim, memoizedCards), [aim, memoizedCards]);
  const selectionType = useSelector((state: State) => state.selection.type);
  useEffect(() => {
    if (selectionType === ElementType.none) {
      setAim(placeholderInitState);
    }
  }, [selectionType]);
  const dispatch = useDispatch();

  placeholder.notify(({ started, x1, x2, y1, line, date = '', done = false }) => {
    if (started !== undefined && !isAimHoversCard) {
      if (started && started !== aim.started) {
        setAim({
          ...aim,
          started,
          date: date,
          x2: aim.x1,
        });
      } else if (aim.started && done) {
        dispatch(
          newCard({
            line: aim.line,
            from: aim.date,
            until: getDateFromDiff(aim.date, getPlaceholderWidth(aim.x1, aim.x2) - 1),
          })
        );
      } else {
        setAim(placeholderInitState);
      }
    }
    if (x1 !== undefined && y1 !== undefined && line !== undefined) {
      if (aim.started === false) {
        if (aim.x1 !== x1 || aim.y1 !== y1) {
          setAim({
            ...aim,
            x1,
            y1,
            line,
            date,
          });
        }
      } else if (x2 !== undefined) {
        setAim({
          ...aim,
          x2,
        });
      }
    }
  });

  const shouldSkipAim = (aim.x1 === 0 && aim.y1 === 0) || isAimHoversCard;
  const { style, size } = getPlaceholderData(aim.x1, aim.x2, aim.y1, shouldSkipAim);

  return (
    <PlaceholderBlock solid={aim.started} style={style}>
      <PlaceholderContent>{size}</PlaceholderContent>
    </PlaceholderBlock>
  );
};

export default React.memo(Placeholder);
