import React, { useEffect } from 'react';
import { useSelector } from 'react-redux';
import UserIcon from '@material-ui/icons/AccountBox';
import EnterIcon from '@material-ui/icons/ExitToApp';

import { handleAuth } from 'data/auth';

import { Spinner } from 'styles/Common/Spinner';
import { AvatarBlock } from 'styles/Header';

import { State } from 'types/state';
import ConnectionStatus from 'components/Header/ConnectionStatus';
import Dropdown from 'components/Header/Dropdown';
import { DropdownWraper } from 'styles/Common/Dropdown';

const Avatar = (): JSX.Element => {
  const user = useSelector((state: State) => state.user);
  useEffect(() => {
    if (user.id !== '') {
      document.body.removeAttribute('class');
    } else {
      document.body.className = 'dark';
    }
  }, [user.id]);

  const UserPhoto = () => {
    // TODO: check if the image is renadable & replace image only when it's loaded
    return user.photoURL ? <img src={user.photoURL} alt={user.name} /> : <UserIcon />;
  };

  if (user.fetchig) {
    return (
      <AvatarBlock>
        <Spinner size={15} />
      </AvatarBlock>
    );
  }
  if (user.id !== '') {
    return (
      <DropdownWraper>
        <AvatarBlock>
          <UserPhoto />
          <ConnectionStatus />
        </AvatarBlock>
        <Dropdown />
      </DropdownWraper>
    );
  }
  return (
    <AvatarBlock onClick={handleAuth} title="Log in">
      <EnterIcon />
    </AvatarBlock>
  );
};

export default React.memo(Avatar);
