import styled from 'styled-components';

const hoverColor = ({ action }: { action?: string }) => (action === 'danger' ? '#d22121' : '#5e88ad');

export const Button = styled.button.attrs({
  type: 'button',
})`
  border: none;
  width: 100%;
  margin: 0;
  padding: 6px 10px 5px;
  font-size: 12px;
  z-index: 1;
  position: relative;
  color: #777;
  background: #f7f8f9;
  box-shadow: 0 2px 6px -1px rgba(0, 0, 0, 0.2), inset 0 0 0 1px #999;
  &:hover:not(:disabled),
  &:focus:not(:disabled) {
    z-index: 2;
    cursor: pointer;
    color: ${hoverColor};
    box-shadow: 0 3px 6px -1px rgba(0, 0, 0, 0.4), inset 0 0 0 1px ${hoverColor};
  }
  svg {
    width: 23px;
    display: none;
    fill: currentColor;
    margin: -3px -7px -5px;
  }
  & + & {
    margin-top: -1px;
  }
  &:disabled {
    color: #ccc;
    background: #eee;
  }
  &:first-child {
    border-radius: 3px 3px 0 0;
  }
  &:last-child {
    border-radius: 0 0 3px 3px;
  }
  @media (max-width: 768px) {
    span {
      display: none;
    }
    svg {
      display: inline-block;
    }
  }
`;
