import React from 'react';

import { FormInputWrap, FormLabel } from 'styles/EditScreen';
import { SwitchBlock } from 'styles/Form/Switch';

type Props = { id: string; value: boolean; toggle: (value: boolean) => void; title?: string };

const Switch = ({ id, value, toggle, title }: Props): JSX.Element => {
  const handleToggle = () => toggle(!value);
  return (
    <React.Fragment>
      {typeof title === 'string' ? <FormLabel htmlFor={id}>{title}</FormLabel> : null}
      <FormInputWrap>
        <SwitchBlock>
          <input type="checkbox" id={id} defaultChecked={value} onChange={handleToggle} />
          <span />
        </SwitchBlock>
      </FormInputWrap>
    </React.Fragment>
  );
};

export default React.memo(Switch);
