import React from 'react';

import { CardStatusSVG } from 'styles/CardBlock';

import { CardTypes } from 'types/card';

// Pending      gray (empty circle)
// In progress  blue (chart pie)
// Delayed     - organge (exclamation)
// Complete    - green (check)
// Dropped      red (cross)

const cardCases = (type: CardTypes): string => {
  switch (type) {
    case 'pending':
      return 'M59 120a30 30 0 100 60 30 30 0 000-60zm93 0a30 30 0 100 60 30 30 0 000-60zm89 0a30 30 0 100 60 30 30 0 000-60z';
    case 'progress':
      return 'M150 150V15A135 135 0 1133.087 217.5L150 150';
    case 'delayed':
      return 'M150 21c-17 0-29 8-29 22v128c0 14 12 22 29 22 16 0 29-9 29-22V43c0-14-13-22-29-22zm0 201a29 29 0 100 58 29 29 0 000-58';
    case 'complete':
      return 'M119 260c-2 2-6 4-9 4s-7-2-10-4l-75-76c-8-8-8-20 0-28l9-10c8-7 21-7 29 0l47 48L237 66c8-8 21-8 29 0l9 10c8 8 8 20 0 28L119 260zm0 0';
    case 'dropped':
      return 'M185 150l66-66c8-7 8-19 0-27l-8-8c-8-8-20-8-27 0l-66 66-66-66c-7-8-19-8-27 0l-8 8c-8 8-8 20 0 27l66 66-66 66c-8 7-8 19 0 27l8 8c8 8 20 8 27 0l66-66 66 66c7 8 19 8 27 0l8-8c8-8 8-20 0-27l-66-66zm0 0';
  }
};

const CardStatus = ({ type, className }: { type?: CardTypes; className?: string }): JSX.Element | null =>
  type ? (
    <CardStatusSVG type={type} className={className}>
      <path d={cardCases(type)} />
    </CardStatusSVG>
  ) : null;

export default React.memo(CardStatus);
